

import './sideform.css';
import Form from 'react-bootstrap/Form';

const Sideform = () => {

    return (

        <>
            <div className="sideform">
                <h4>Query for Visa Application</h4>
                <Form>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Control
                            type="text"
                            placeholder="Enter Your Name"
                            autoFocus
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput3">
                        <Form.Control
                            type="email"
                            placeholder="Enter Your Email"
                            autoFocus
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput2">
                        <Form.Control
                            type="text"
                            placeholder="Enter Your Number"
                            autoFocus
                        />
                    </Form.Group>
                    <Form.Select className="mb-3" required>
                        <option value="" disabled selected>Select Country</option>
                        <option value="1">Australia</option>
                        <option value="1">Canada</option>
                        <option value="1">Austria</option>
                    </Form.Select>
                      <button className="mb-3">Submit</button>
                      <p className='terms text-white'>By submitting, you acknowledge that you have read and understood the Data Protection Notice and consent to the collection, use and disclosure of your personal data for the purpose set out in the notice.</p>
                </Form>
            </div>
        </>

    )
}

export default Sideform